body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.flag {
  width: 250px;
  aspect-ratio: 1;
}

.App {
  display: grid;
  grid-template-rows: 1fr 11fr 0.5fr;
  height: 100vh;
}

footer {
  font-size: small;
  background-color: black;
  color: white;
  padding:0.5rem
}

header {
  font-size: 2rem;
  background-color: black;
  color: white;
  padding: 1rem;
}

input {
  margin: 10px;
  border: 2px solid black;
  border-radius: 10px;
  padding: 0.5rem
}

ul {
  list-style: none;
}

.display {
  display: grid;
  justify-content: center;
}

.img {
  width: 250px;
  height: auto;
}